.card {
	width: 30vw;

	position: fixed !important;
	top:10%;
	left: 50%;
	transform: translate(-50%, 0);
	z-index: 1000;

	.card-body {
		padding-left:2rem;
		padding-right:2rem;
	}

}

.backdrop {
	position:fixed;
	top:0;
	bottom:0;
	left:0;
	right:0;

	background-color: rgba(0,0,0,0.7);
}